/* description of classes*/
/*navigat bar*/
.pagetitle {
  height: 30px;
  font-size: 140%;
  margin: 0 0 5px 0;
  font-weight: bold;
  text-align: center;
  color: rgb(229, 229, 236);

  background-color: #130a74;
}

.pagetitle:hover {
  color: yellow;
  cursor: pointer;
}

.img_style {
  width: 50%;
  height: auto;
  margin: 20px auto;
  place-items: center;
  display: block;
  border: solid 2px;
  border-color: white;
  transition: transform 0.3s ease-in-out;
}

