/****************  buttons  ******************/
.button {
  height: auto;
  width: 120px;
  font-family: Cambria;
  background-color: #1b0cbd;
  color: white;
  cursor: pointer;
}
.button.active {
  height: auto;
  width: 120px;
  font-family: Cambria;
  background-color: black;
  color: yellow;
}
button:active {
  background-color: black;
}
